<template lang="pug">
    .authentication-wrapper.authentication-1.px-4
        .authentication-inner.py-5
            // Logo
            .d-flex.justify-content-center.align-items-center
            .ui-w-60
                .w-100.position-relative(style='padding-bottom: 54%')
                img(:src='M.LOGOBG',  style='max-width: 300px;')
            // / Logo
            // Form
            form.my-5
                b-form-group(label='Usuario o Email')
                    b-input(v-model='credentials.username')
                b-form-group
                    .d-flex.justify-content-between.align-items-end(slot='label')
                        div Contrase単a
                        a.d-block.small(href='javascript:void(0)') Olvidaste tu contrase単a?
                    b-input(type='password' v-model='credentials.password')
                .d-flex.justify-content-between.align-items-center.m-0
                    b-check.m-0(v-model='credentials.rememberMe') Recuerdame
                    b-btn(variant='primary' @click="login(credentials)") ENTRAR
                    b-btn(variant='primary' @click="fullscreen()") FULLSCREEN
            // / Form
            .text-center.text-muted
                | {{M.TITLE}} :: {{M.SLOGAN}}
</template>

<style src="@/vendor/styles/pages/authentication.scss" lang="scss"></style>

<script>
    export default {
        name: 'login',
        metaInfo: {
            title: require('@/../../package.json').title,
        },
        data() {
            return {
                credentials:{
                    username: 'gcastellan0s',
                    password: 'H1dr4Gu51*',
                    rememberMe: false
                }
            };
        },
        methods: {
            login({username, password, code=this.M.CODE}) {
                ax.post('/rest-auth/login/',{username,password});
            },
            successFunction(){
                console.info("It worked!");
            },
            errorFunction(error){
                console.error(error);
            },
            trace(value){
                console.log(value);
            },
            fullscreen(value){
                AndroidFullScreen.immersiveMode(successFunction, errorFunction);
            },
        },
        mounted: function () {
            console.log(cordova);
            //var ref = cordova.InAppBrowser.open('http://pasteleriaslaestrella.pasteleriaslaestrella.com/facturar-ticket-de-compra/','_blank','location=no,toolbar=no,zoom=no');
            //var ref = cordova.InAppBrowser.open('http://apache.org', '_blank', 'location=yes');
            //console.log(ref);
        },
    }
</script>


